import React, { useState} from "react";
import { Axios } from "../../services/axios.service";
import Formulario from "../forms/form.component";
import StateList from './listar.component'

const CreateState = () =>{
    
    const [formValues] = useState ({name:'', states:''})
    
    const onSubmit = stateObject =>{
        Axios.post('/estados/create-state', stateObject)
            .then( res => {
                if (res.status === 200){
                    alert('Estado creado satisfactoriamente');
                    window.location.reload();}
                else    
                    Promise.reject()    
            })
            .catch(err => alert('El estado no se pudo crear'))
    }
    return(
        
        <div className="states">
            <h2>Crear Estado</h2>
            <Formulario 
                initialValues={formValues}
                onSubmit = {onSubmit}
                enableReinitialize>
                    Crear Estado
            </Formulario>
            <hr/>
            <h2>Lista de Estados</h2>
            <StateList />
        </div>

    )
}

export default CreateState