import React, { useState, useEffect} from "react";
import {  useParams } from 'react-router-dom';
import { Axios } from "../../services/axios.service";
import InventaryForm from "../forms/inventaryform.component";
import { useNavigate } from 'react-router-dom';

const EditActivo = () => {
    const [formValues, setFormValues] = useState ({serial:'', models:'', description:'', color:'', picture:'', price:'', usuario:'', tipos:''});
    const { id } = useParams();
    const navigate = useNavigate();
    
    const onSubmit = (activoObjetc) =>{
        Axios.put('/activos/update-activo/' + id, activoObjetc)
            .then((res) => {
                if (res.status === 200){
                    alert("Activo actualizado correctamente");
                    navigate("/activos/list-activo");                 
                }
                else    Promise.reject();
            })
            .catch((err) => alert("No se pudo realizar la actualizacin del activo"));
    };

    useEffect(() => {
        Axios.get('/activos/edit-activo/'+ id)
            .then((res) => {
                const {serial,  models, description, color, picture, price, purchasedate, usuario, tipos} = res.data;
                setFormValues({ serial,  models, description, color, picture,  price, purchasedate, usuario, tipos});
            })
            .catch((err) => console.log(err));
    },[id]);
    
    return(
        
        <div classserial="activos">
            <h2>Actualizar Activo</h2>
            <InventaryForm 
                initialValues={formValues}
                onSubmit = {onSubmit}
                enableReinitialize>
                    Actualizar Activo
            </InventaryForm>
        </div>

    )
};

export default EditActivo;