import React, { useState} from "react";
import { Axios } from "../../services/axios.service";
import InventaryForm from "../forms/inventaryform.component";

const CreateActivo = () =>{

    const [formValues] = useState ({serial:'', models:'', description:'', color:'',picture:'', price:'',purchasedate:'', estadoEquipo:''})
    
    const onSubmit = formValues =>{
        Axios.post('/activos/create-activo', formValues)
            .then( res => {
                if (res.status === 200){
                    alert('Activo creado satisfactoriamente');
                   // window.location.reload();
                }
                else    
                    Promise.reject('Datos enviados incorrectamente')    
            })
            .catch(err => alert('El activo no se pudo crear'))
    }
    return(
        
        <div classserial="activos">
            <h2>Crear Activo</h2>
            <InventaryForm 
                initialValues={formValues}
                onSubmit = {onSubmit}
                enableReinitialize>
                    Crear Activo
            </InventaryForm>
        </div>

    )
}

export default CreateActivo