import React from "react";
import {Link} from 'react-router-dom';
import { Axios } from "../../services/axios.service";
import { Button } from "react-bootstrap";
//import moment from 'moment';


const StateTable = (props) => {
    const { _id, name, states, creationdate, modificationdate} = props.obj;
    
    const deleteState = () => {
        Axios.delete('/estados/delete-state/'+_id)
            .then((res) => {
                if( res.status === 200){
                    alert("Estado borrado correctamente");
                    window.location.reload();
                }else Promise.reject();
            })
            .catch((err)=>alert("Estado no pudo ser borrado"));
    };
    
    return(

         <tr>
            <td>{name}</td>
            <td className="estados">{states}</td>
            <td>{creationdate}</td>
            <td>{modificationdate}</td>
            <td className="botones">
                <Link 
                    className="edit-link"
                    to={'/estados/edit-state/'+_id}><Button size="sm" variant="info">
                        EDITAR</Button>
                </Link>
                <Button 
                    onClick={deleteState}
                    size="sm" variant="danger">
                        BORRRAR
                </Button>
            </td>
        </tr>
        
    );
};

export default StateTable;