import React, { useState, useEffect } from "react";
import { Axios } from "../../services/axios.service";
import ActivosTable from './statetable.component';

import '../../App.css'

const ActivosList = () => {
    
    const [inventary, setInventary] = useState([]);

    useEffect(() => {
        Axios.get('/activos/list-activo')
            .then(({ data }) => {
                setInventary(data);
            })
            .catch((error) => {
                console.log(error);
            });
    }, []);

    const Data4Table = () => {
        return inventary.map((res, i) => {
            return <ActivosTable obj={res} key={i} />;
        });
    };

    return ( 
        <div className="container-fluid">
            <div className="mt-2 mb-2 row row-cols-1 row-cols-md-3 g-4">           
                    {Data4Table()}
                </div>    
        </div>
    );
};

export default ActivosList