import React, { useState, useEffect} from "react";
import {  useParams } from 'react-router-dom';
import { Axios } from "../../services/axios.service";
import Formulario from "../forms/form.component";
import StateList from './listar.component';
import { useNavigate } from 'react-router-dom';

const EditState = () => {
    const [formValues, setFormValues] = useState ({name:'', states:''});
    const { id } = useParams();
    const navigate = useNavigate();
    
    const onSubmit = (stateObject) =>{
        Axios.put('/estados/update-state/' + id, stateObject)
            .then((res) => {
                if (res.status === 200){
                    alert("Estado actualizado correctamente");
                    navigate("/estados/create-state");                 
                }
                else    Promise.reject();
            })
            .catch((err) => alert("No se pudo realizar la actualizacin del estado"));
    };

    useEffect(() => {
        Axios.get('/estados/edit-state/'+ id)
            .then((res) => {
                const {name, states} = res.data;
                setFormValues({ name, states});
            })
            .catch((err) => console.log(err));
    },[id]);
    
    return(

        <div className="estados">
            <h2>Actualizar Estado</h2>
            <Formulario
                initialValues = {formValues}
                onSubmit = {onSubmit}
                enableReinitialize>
                    Actualizar Estado
            </Formulario>
            <hr/>
            <h2>Lista de Estados</h2>
            <StateList />
        </div>        
    );
};

export default EditState;