import {BrowserRouter, Route, Routes} from 'react-router-dom';
import CreateUser from '../components/usuarios/crear.component';
import UserList from '../components/usuarios/listar.component';
import EditUser from '../components/usuarios/editar.component';
import CreateType from '../components/tipos/crear.component';
import TypeList from '../components/tipos/listar.component';
import EditType from '../components/tipos/editar.component';
import BrandList from '../components/marcas/listar.component';
import CreateBrand from '../components/marcas/crear.component';
import EditBrand from '../components/marcas/editar.component';
import StateList from '../components/estados/listar.component';
import CreateState from '../components/estados/crear.component';
import EditState from '../components/estados/editar.component';
import CreateActivo from '../components/activos/crear.component';
import MenuResponsive from '../components/headers/navbarresponsive';
import ActivosList from '../components/activos/listar.component';
import EditActivo from '../components/activos/editar.component';


function Rutas() {
    return(
        <BrowserRouter>
            <MenuResponsive/>
            <Routes>
                <Route exact path='/usuarios/create-user' element={<CreateUser/>}></Route>
                <Route exact path='/usuarios/list-user' element={<UserList/>}></Route>
                <Route path='/usuarios/edit-user/:id' element={<EditUser/>}></Route>
                <Route exact path='/tipos/create-type' element={<CreateType/>}></Route>
                <Route exact path='/tipos/list-type' element={<TypeList/>}></Route>
                <Route path='/tipos/edit-type/:id' element={<EditType/>}></Route>
                <Route exact path='/marcas/list-brand' element={<BrandList/>}></Route>
                <Route exact path='/marcas/create-brand' element={<CreateBrand/>}></Route>
                <Route path='/marcas/edit-brand/:id' element={<EditBrand/>}></Route>
                <Route exact path='/estados/list-state' element={<StateList/>}></Route>
                <Route exact path='/estados/create-state' element={<CreateState/>}></Route>
                <Route path='/estados/edit-state/:id' element={<EditState/>}></Route>
                <Route exact path='/activos/create-activo' element={<CreateActivo/>}></Route>
                <Route path='/activos/list-activo' element={<ActivosList/>}></Route>
                <Route path='/activos/edit-activo/:id' element={<EditActivo/>}></Route>
                <Route  path='/' element={<ActivosList />}></Route>
                <Route  path='*' element={<ActivosList />}></Route>
            </Routes> 
      </BrowserRouter>
    ) 
}

export default Rutas