import React, { useState} from "react";
import { Axios } from "../../services/axios.service";
import Formulario from "../forms/form.component";
import BrandList from './listar.component';

const CreateBrand = () =>{
    
    const [formValues] = useState ({name:'', states:''})
    
    const onSubmit = brandObject =>{
        Axios.post('/marcas/create-brand', brandObject)
            .then( res => {
                if (res.status === 200){
                    alert('Marca creada satisfactoriamente');
                    window.location.reload();}
                else    
                    Promise.reject()    
            })
            .catch(err => alert('La marca no se pudo crear'))
    }
    return(
        
        <div className="marcas">
            <h2>Crear Marca</h2>
            <Formulario 
                initialValues={formValues}
                onSubmit = {onSubmit}
                enableReinitialize>
                    Crear Marca
            </Formulario>
            <hr/>
            <h2>Lista de Marcas</h2>
            <BrandList />
        </div>
    )
}

export default CreateBrand