import React, { useState, useEffect} from "react";
import { useNavigate, useParams } from 'react-router-dom';
import { Axios } from "../../services/axios.service";
import Formulario from "../forms/form.component";
import BrandList from './listar.component';

const EditBrand = () => {
    const [formValues, setFormValues] = useState ({name:'', states:''});
    const { id } = useParams();
    const navigate = useNavigate();
    
    const onSubmit = (brandObject) =>{
        Axios.put('/marcas/update-brand/' + id, brandObject)
            .then((res) => {
                if (res.status === 200){
                    alert("Marca actualizada correctamente");
                    navigate("/marcas/create-brand");                 
                }
                else    Promise.reject();
            })
            .catch((err) => alert("No se pudo realizar la actualizacin de la Marca"));
    };

    useEffect(() => {
        Axios.get('/marcas/edit-brand/'+ id)
            .then((res) => {
                const {name, states} = res.data;
                setFormValues({ name, states});
            })
            .catch((err) => console.log(err));
    },[id]);
    
    return(

        <div className="marcas">
            <h2>Actualizar Marca</h2>
            <Formulario
                initialValues = {formValues}
                onSubmit = {onSubmit}
                enableReinitialize>
                    Actualizar Marca
            </Formulario>
            <hr/>
            <h2>Lista de Marcas</h2>
            <BrandList />
        </div>        
    );
};

export default EditBrand;