import React, { useState, useEffect } from "react";
import { Link } from 'react-router-dom';
import * as Yup from "yup";
import { Formik, Form, Field, ErrorMessage } from "formik";
import { FormGroup, Button } from "react-bootstrap";
import '../../App.css'
import { getUsuario } from "../../services/listar.service";
import { getBrand } from "../../services/listar.service";
import { getType } from "../../services/listar.service";
import { getState } from "../../services/listar.service";

const InventaryForm = (props) => {
    const validationSchema = Yup.object().shape({
        serial: Yup.string()
            .required("Serial Requerido")
            .min(3, `Mnimo 3 caracteres`)
            .max(50, `Mximo 50 caracteres`),
        models: Yup.string()
            .required("Modelo Requerido")
            .min(3, `Mnimo 3 caracteres`)
            .max(50, `Mximo 50 caracteres`),
        color: Yup.string()
            .required("Color Requerido")
            .min(3, `Mnimo 3 caracteres`)
            .max(50, `Mximo 50 caracteres`),
        description: Yup.string()
            .required("Descripcin Requerido")
            .min(3, `Mnimo 3 caracteres`)
            .max(50, `Mximo 50 caracteres`),
        price: Yup.number()
            .required("Precio Requerido")
            .positive('Debe ser maor 0 igual a cero'),

    });

    //const [usuario, setUsuario] = useState([]);
    const [marcas, setMarcas] = useState([]);
    const [type, setType] = useState([]);
    const [estadoEquipo, setState] = useState([]);
    const [usuario, setUsuario] = useState([]);

    const [valoresForm, setValoresForm] = useState({});


    const handleOnChange = ({ target }) => {

        const { name, value } = target;


        setValoresForm({ ...valoresForm, [name]: value })
    }

    //const [estados, SetEstados] = useState("");

    useEffect(() => {
        async function resp() {
            try {
                const { data } = await getUsuario();
                setUsuario(data)

            } catch (error) {
                console.log(error);
            }
        }
        resp()
    }, [])

    useEffect(() => {
        async function resp() {
            try {
                const { data } = await getBrand();
                setMarcas(data)
            } catch (error) {
                console.log(error);
            }
        }
        resp()
    }, [])

    useEffect(() => {
        async function resp() {
            try {
                const { data } = await getType();
                setType(data)
            } catch (error) {
                console.log(error);
            }
        }
        resp()
    }, [])

    useEffect(() => {
        async function resp() {
            try {
                const { data } = await getState();
                setState(data)
            } catch (error) {
                console.log(error);
            }
        }
        resp()
    }, [])



    return (
        <div className="form-wrapper">
            <Formik {...props} validationSchema={validationSchema}>
                <Form className="InventaryForm">
                    <FormGroup className="mb-4">
                        <div className="col">
                            <Field className="form-control" type="text" id="serial"
                                name="serial"
                                placeholder="Serial" />

                            <ErrorMessage name="serial"
                                className="d-block invalid-feedback"
                                component="span" />
                        </div>
                        <div className='col'>
                            <Field className="form-control" type="text" id="models"
                                name="models"

                                placeholder="Modelo" />

                            <ErrorMessage name="models"
                                className="d-block invalid-feedback"
                                component="span" />
                        </div>
                        <div className='col'>
                            <Field className="form-control" id="description" type="text"
                                name="description"

                                placeholder="Descripcin" />

                            <ErrorMessage name="description"
                                className="d-block invalid-feedback"
                                component="span" />
                        </div>
                        <div className="col">
                            <Field className="form-control" id="color" type="text"
                                name="color"

                                placeholder="Color" />

                            <ErrorMessage name="color"
                                className="d-block invalid-feedback"
                                component="span" />
                        </div>
                    </FormGroup>

                    <FormGroup className="mb-4">
                        <div className="col">

                            <label htmlFor="picture" className="form-label">Imagen</label>
                            <Field className="form-control" id="picture"
                                name="picture"
                                placeholder="Imagen" />

                            <ErrorMessage name="picture"
                                className="d-block invalid-feedback"
                                component="span" />
                        </div>
                        <div className="col">
                            <label htmlFor="purchasedate" className="form-label">Fecha de Compra</label>
                            <Field className="form-control" id="purchasedate" type='date'
                                name="purchasedate" onClick={(e) => handleOnChange(e)} />

                            <ErrorMessage name="purchasedate"
                                className="d-block invalid-feedback"
                                component="span" />
                        </div>
                        <div className="col">
                            <label htmlFor="price" className="form-label">Precio</label>
                            <Field className="form-control" id="price" type="text"
                                name="price"
                                placeholder="Precio" />
                            <ErrorMessage name="price"
                                className="d-block invalid-feedback"
                                component="span" />
                        </div>
                        <div className="col">
                            <label htmlFor="form-label">Usuario</label>
                            <select className="form-select"
                                defaultValue={"default"}
                                name='usuarios'>
                                <option value="">Escoja una opcin.</option>
                                {
                                    usuario.map((user) => {
                                        return <option key={user._id} value={user._id}>{user.name}</option>
                                    })
                                }
                            </select>
                        </div>

                    </FormGroup>
                    <FormGroup className="mb-4">

                        <div className="col">
                            <label htmlFor="form-label">Marca</label>
                            <select className="form-select"
                                defaultValue={"default"}
                                name='marcas'>
                                <option value="">Escoja una opcin.</option>
                                {
                                    marcas.map((marca) => {
                                        return <option key={marca._id} value={marca._id}>{marca.name}</option>
                                    })
                                }
                            </select>
                        </div>
                        <div className="col">
                            <label htmlFor="Tipo">Tipo de Equipo</label>
                            <select className="form-select"
                                defaultValue={"default"}
                                name='estadoEquipo'>
                                <option value="">Escoja una opcin.</option>
                                {
                                    type.map((tipo) => {
                                        return <option key={tipo._id} value={tipo._id}>{tipo.name}</option>
                                    })
                                }
                            </select>
                        </div>
                        <div className="col">
                            <label htmlFor="Estado">Estado de Equipo</label>
                            <select
                                className="form-control"
                                defaultValue={"default"}
                                onChange={(e) => handleOnChange(e)}

                            >
                                <option disabled selected>
                                    Selecione
                                </option>
                                {estadoEquipo.map((estados) => (
                                    <option key={estados._id} value={estados._id}>
                                        {estados.name}
                                    </option>
                                ))}
                            </select>;
                        </div>
                    </FormGroup>
                    <div className="botones-crear">
                        <Button variant="success" size="md"
                            block="block" type="submit">
                            {props.children}
                        </Button>

                        <Link to={`/`}><button className="btn btn-primary">INVENTARIO</button></Link>
                    </div>
                </Form>
            </Formik>

        </div>
    );
};

export default InventaryForm;