import React, { useState, useEffect} from "react";
import { useNavigate, useParams } from 'react-router-dom';
import { Axios } from "../../services/axios.service";
import UserForm from "../forms/userform.component";
import UserList from './listar.component'

const EditUser = () => {
    const [formValues, setFormValues] = useState ({name:'', email:'', states:''});
    const { id } = useParams();
    const navigate = useNavigate();
    
    const onSubmit = (userObject) =>{
        Axios.put('/usuarios/update-user/' + id, userObject)
            .then((res) => {
                if (res.status === 200){
                    alert("Usuario actualizado correctamente");
                    navigate("/usuarios/create-user");                 
                }
                else    Promise.reject();
            })
            .catch((err) => alert("No se pudo realizar la actualizacin del usuario"));
    };

    useEffect(() => {
        Axios.get('/usuarios/edit-user/'+ id)
            .then((res) => {
                const {name, email, states} = res.data;
                setFormValues({ name, email, states});
            })
            .catch((err) => console.log(err));
    },[id]);
    
    return(

        <div className="usuarios">
            <h2>Actualizar Usuario</h2>
            <UserForm
                initialValues = {formValues}
                onSubmit = {onSubmit}
                enableReinitialize>
                    Actualizar Usuario
            </UserForm>
            <hr/>
            <h2>Lista de Usuarios</h2>
            <UserList />  
        </div> 
      
    );


};

export default EditUser;