import React from "react";
import {Link} from 'react-router-dom';
import '../../App.css'


const ActivosTable = (props) => {
    const {_id, serial,  models, description, color, picture, price} = props.obj;
    
    return(
        <div className="col">
            <div className="card">
                <img src={'/img/'+picture} className="card-img-top" alt="..."/>
                <div className="card-body">
                    <h5 className="card-title">Caracteristicas</h5>
                    <hr/>
                    <p className="card-text">{`Serial : ${serial}`}</p>
                    <p className="card-text">{`Modelo : ${models}`}</p>
                    <p className="card-text">{`Descripcin : ${description}`}</p>
                    <p className="card-text">{`color : ${color}`}</p>
                    <p className="card-text">{`precio : ${price}`}</p>
                    
                    <Link 
                    className="card-text"
                    to={'/activos/edit-activo/'+_id}>Ver mas...
                </Link>
                </div>

            </div>
        </div>

    );
};

export default ActivosTable;