import React, { useState, useEffect } from "react";
import { Axios } from "../../services/axios.service";
import { Table } from "react-bootstrap";
import UserTable from "./usertable.component";
import '../../App.css'

const UserList = () => {
    const [user, setUser] = useState([]);

    const getUser = async () =>{

       try {
            const { data } = await Axios.get('/usuarios/list-user');
            setUser(data);
        } catch (error) {
            console.log(error);
        }
        }        
    useEffect(() => {
            getUser();
        }, []);
   
    const DataTable = () => {
        return user.map((res, i) => {
            return <UserTable obj={res} key={i} />;
        });
    };

    return (

        <div>
           <div className="fila-crear">
           <hr/>
            </div>          
            <div className="table-wrapper">
                <Table striped bordered hover>
                    <thead>
                        <tr>
                            <th>Nombre</th>
                            <th>Correo Elctronico</th>
                            <th>Estado</th>
                            <th>Fecha de creacin</th>
                            <th>Fecha de Modificacin</th>
                            <th>Acciones</th>
                        </tr>
                    </thead>
                    <tbody>{DataTable()}</tbody>
                </Table>
            </div>
        </div>
    );
};

export default UserList