import React from "react";
import {Link} from 'react-router-dom';
import { Axios } from "../../services/axios.service";
import { Button } from "react-bootstrap";

const TypeTable = (props) => {
    const { _id, name, states, creationdate, modificationdate} = props.obj;
    
    const deleteType = () => {
        Axios.delete('/tipos/delete-type/'+_id)
            .then((res) => {
                if( res.status === 200){
                    alert("Tipo borrado correctamente");
                    window.location.reload();
                }else Promise.reject();
            })
            .catch((err)=>alert("Tipo no pudo ser borrado"));
    };
    
    return(

         <tr>
            <td>{name}</td>
            <td className="estados">{states}</td>
            <td>{creationdate}</td>
            <td>{modificationdate}</td>
            <td className="botones">
                <Link 
                    className="edit-link"
                    to={'/tipos/edit-type/'+_id}><Button size="sm" variant="info">
                        EDITAR</Button>
                </Link>
                <Button 
                    onClick={deleteType}
                    size="sm" variant="danger">
                        BORRRAR
                </Button>
            </td>
        </tr>
        
    );
};

export default TypeTable;