import React from "react";
import {Link} from 'react-router-dom';
import { Axios } from "../../services/axios.service";
import { Button } from "react-bootstrap";


 
const BrandTable = (props) => {
    const { _id, name, states, creationdate, modificationdate} = props.obj;

    const deleteBrand = () => {
        Axios.delete('/marcas/delete-brand/'+_id)
            .then((res) => {
                if( res.status === 200){
                    alert("Marca borrada correctamente");
                    window.location.reload();
                }else Promise.reject();
            })
            .catch((err)=>alert("Marca no pudo ser borrada"));
    };
    
    return(
        
         <tr>
            <td>{name}</td>
            <td className="estados">{states}</td>
            <td>{creationdate}</td>
            <td>{modificationdate}</td>
          
            <td className="botones">
                <Link 
                    className="edit-link"
                    to={'/marcas/edit-brand/'+_id}><Button size="sm" variant="info">
                        EDITAR</Button>
                </Link>
                <Button 
                    onClick={deleteBrand}
                    size="sm" variant="danger">
                        BORRRAR
                </Button>
            </td>
        </tr>
        
    );
};

export default BrandTable;