import React from "react";
import {Link} from 'react-router-dom';
import * as Yup from "yup";
import { Formik, Form, Field, ErrorMessage } from "formik";
import { FormGroup, Button } from "react-bootstrap";
import '../../App.css'

const UserForm = (props) =>{
    const valuserSchema = Yup.object().shape({
        name: Yup.string()
        .required("Nombre Requerido")
        .min(3, `Minimo 3 caracteres`)
        .max(50, `Mximo 50 caracteres`),
        email: Yup.string()
        .email(" Entraste una direccin de correo invalida")
        .required("Correo Requerido"),
        states: Yup.string().required("Estado Requerido"),
    });
    
    return(
        <div className="form-wrapper">
            <Formik {...props} validationSchema={valuserSchema}>
                <Form className="userform">
                    <FormGroup className="mb-3">
                    <div className="form-floating mb-3">
                        <Field name="name" type="text"      
                        className="form-control" id="floatingInput" placeholder="Nombre"/>
                        <label htmlFor="floatingInput">Nombre</label>
                        <ErrorMessage name="name"
                        className="d-block invalid-feedback"
                        component="span" />
                    </div>
                    </FormGroup>
                    <FormGroup className="mb-3">
                    <div className="form-floating mb-3">
                        <Field name="email" type="text" 
                        className="form-control" id="floatingInput" placeholder="Correo Electronico"/>
                        <label htmlFor="floatingInput">Email</label>
                        <ErrorMessage name="email"
                        className="d-block invalid-feedback"
                        component="span" />
                     </div>   
                    </FormGroup>
                    <FormGroup className="mb-3">
                    <div className="form-floating mb-3">
                        <Field name="states" type="text" 
                        className="form-control" id="floatingInput" placeholder="Estado"/>
                        <label htmlFor="floatingInput">Estado</label>
                        <ErrorMessage name="states"
                        className="d-block invalid-feedback"
                        component="span" />
                     </div>                 
                    </FormGroup>
                    <div className="botones-crear">
                    <Button variant="success" size="md"
                        block="block" type="submit">
                        {props.children}
                    </Button>
                    
                    <Link to={`/`}><button className="btn btn-primary">INVENTARIO</button></Link>
                    </div>
                </Form>
            </Formik>
            
        </div>
    );
};

export default UserForm;