import React, { useState} from "react";
import { Axios } from "../../services/axios.service";
import Formulario from "../forms/form.component";
import TypeList from "./listar.component";

const CreateType = () =>{
    
    const [formValues] = useState ({name:'', states:''})
    
    const onSubmit = typeObject =>{
        Axios.post('/tipos/create-type', typeObject)
            .then( res => {
                if (res.status === 200){
                    alert('Tipo creado satisfactoriamente');
                    window.location.reload();                
                }
                else    
                    Promise.reject()    
            })
            .catch(err => alert('El Tipo no se pudo crear'))
    }
    return(
        
        <div className="tipos">
            <h2>Crear Tipo</h2>
            <Formulario 
                initialValues={formValues}
                onSubmit = {onSubmit}
                enableReinitialize>
                    Crear Tipo
            </Formulario>
            <hr/>
            <h2>Lista de Tipos</h2>
            <TypeList />
        </div>

    )

}

export default CreateType