import React, { useState, useEffect} from "react";
import { useNavigate, useParams } from 'react-router-dom';
import { Axios } from "../../services/axios.service";
import Formulario from "../forms/form.component";
import TypeList from "./listar.component";

const EditType = () => {
    const [formValues, setFormValues] = useState ({name:'', states:''});
    const { id } = useParams();
    const navigate = useNavigate();
    
    const onSubmit = (typeObject) =>{
        Axios.put('/tipos/update-type/' + id, typeObject)
            .then((res) => {
                if (res.status === 200){                   
                    alert("Tipo actualizado correctamente");
                    navigate("/tipos/create-type");
                }
                else    Promise.reject();
            })
            .catch((err) => alert("No se pudo realizar la actualizacin del Tipo"));
    };

    useEffect(() => {
        Axios.get('/tipos/edit-type/'+ id)
            .then((res) => {
                const {name, states} = res.data;
                setFormValues({ name, states});
            })
            .catch((err) => console.log(err));
    },[id]);
    
    return(

        <div className="tipos">
            <h2>Actualizar Tipo</h2>
            <Formulario
                initialValues = {formValues}
                onSubmit = {onSubmit}
                enableReinitialize>
                    Actualizar Tipo
                </Formulario>
                <hr/>
                <h2>Lista de Tipos</h2>
                <TypeList />   
        </div>        
    );
};

export default EditType;