import React, { useState} from "react";
import { Axios } from "../../services/axios.service";
import UserForm from "../forms/userform.component";
import UserList from './listar.component'


const CreateUser = () =>{
    
    const [formValues] = useState ({name:'', email:'', states:''})
    
    const onSubmit = userObject =>{
        Axios.post('/usuarios/create-user', userObject)
            .then( res => {
                if (res.status === 200){
                    alert('Usuario creado satisfactoriamente');
                    window.location.reload();}
                else    
                    Promise.reject()    
            })
            .catch(err => alert('El usuario no se pudo crear'))
    }
    return(
        
        <div className="usuarios">
            <h2>Crear Usuario</h2>
            <UserForm 
                initialValues={formValues}
                onSubmit = {onSubmit}
                enableReinitialize>
                    Crear Usuario
            </UserForm>
            <hr/>
            <h2>Lista de Usuarios</h2>
            <UserList />
        </div>

    )

}

export default CreateUser