import React, { useState, useEffect } from "react";
import { Axios } from "../../services/axios.service";
import { Table } from "react-bootstrap";
import StateTable from "./statetable.component";
import '../../App.css'

const StateList = () => {
    const [states, setStates] = useState([]);

    useEffect(() => {
        Axios.get('/estados/list-state')
            .then(({ data }) => {
                setStates(data);
            })
            .catch((error) => {
                console.log(error);
            });
    }, []);

    const Data4Table = () => {
        return states.map((res, i) => {
            return <StateTable obj={res} key={i} />;
        });
    };

    return (

        <div>
           <div className="fila-crear">
             <hr/> 
            </div>          
            <div className="table-wrapper">
                <Table striped bordered hover>
                    <thead>
                        <tr>
                            <th>Nombre</th>
                            <th>Estado</th>
                            <th>Fecha de creacin</th>
                            <th>Fecha de Modificacin</th>
                            <th>Acciones</th>
                        </tr>
                    </thead>
                    <tbody>{Data4Table()}</tbody>
                </Table>
            </div>
        </div>
    );
};

export default StateList