import React from "react";
import {Link} from 'react-router-dom';
import { Axios } from "../../services/axios.service";
import { Button } from "react-bootstrap";
 
const UserTable = (props) => {
    const { _id, name, email, states, creationdate, modificationdate} = props.obj;
    
    const deleteUser = () => {
        Axios.delete('/usuarios/delete-user/'+_id)
            .then((res) => {
                if( res.status === 200){
                    alert("Usuario borrado correctamente");
                    window.location.reload();
                }else Promise.reject();
            })
            .catch((err)=>alert("Usuario no pudo ser borrado"));
    };
    
    return(

         <tr className="tabla">
            <td>{name}</td>
            <td>{email}</td>
            <td className="estados">{states}</td>
            <td>{creationdate}</td>
            <td>{modificationdate}</td>
            <td className="botones">
                <Link 
                    className="edit-link"
                    to={'/usuarios/edit-user/'+_id}><Button size="sm" variant="info">
                        EDITAR</Button>
                </Link>
                <Button 
                    onClick={deleteUser}
                    size="sm" variant="danger">
                        BORRRAR
                </Button>
            </td>
        </tr>
        
    );
};

export default UserTable;