import {NavLink} from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import { Nav, Navbar } from 'react-bootstrap';

function MenuResponsive() {

  return (
    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
      <Container>
        <NavLink activeclassname="active" to="/activos/list-activo">INVENTARIO</NavLink>
        
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          
          <Nav className="navbar-nav me-auto">
            <Nav.Item className="nav-item">
              <NavLink className='nav-link' activeclassname="active" to="/activos/create-activo" >ACTIVOS</NavLink>
            </Nav.Item>
          </Nav>
          
          <Nav className="navbar-nav mr-auto">
            <Nav.Item className="nav-item">
              <NavLink className='nav-link' activeclassname="active" to="/usuarios/create-user" >USUARIOS</NavLink>
            </Nav.Item>
            <Nav.Item className="nav-item" >
              <NavLink  className='nav-link' activeclassname="active" to="/tipos/create-type">TIPOS</NavLink>
            </Nav.Item>
            <Nav.Item className="nav-item">
              <NavLink  className="nav-link" activeclassname="active" to="/marcas/create-brand">MARCAS</NavLink>
            </Nav.Item>
            <Nav.Item className="nav-item">
                <NavLink className="nav-link" activeclassname="active" to="/estados/create-state">ESTADOS</NavLink>
            </Nav.Item>
          </Nav>
          
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default MenuResponsive;