import React, { useState, useEffect } from "react";
import { Axios } from "../../services/axios.service";
import { Table } from "react-bootstrap";
import BrandTable from "./brandtable.component";
import '../../App.css'

const BrandList = () => {
    const [brand, setBrand] = useState([]);

    useEffect(() => {
        Axios.get('/marcas/list-brand')
            .then(({ data }) => {
                setBrand(data);
            })
            .catch((error) => {
                console.log(error);
            });
    }, []);

    const Data3Table = () => {
        return brand.map((res, i) => {
            return <BrandTable obj={res} key={i} />;
        });
    };

    return (

        <div>
           <div className="fila-crear">
           <hr/>
            </div>          
            <div className="table-wrapper">
                <Table striped bordered hover>
                    <thead>
                        <tr>
                            <th>Nombre</th>
                            <th>Estado</th>
                            <th>Fecha de creacin</th>
                            <th>Fecha de Modificacin</th>
                            <th>Acciones</th>
                        </tr>
                    </thead>
                    <tbody>{Data3Table()}</tbody>
                </Table>
            </div>
        </div>
    );
};

export default BrandList