import React, { useState, useEffect } from "react";
import { Axios } from "../../services/axios.service";
import {Table } from "react-bootstrap";
import TypeTable from "./typetable.component";
import '../../App.css'

const TypeList = () => {
    const [tipo, setTipo] = useState([]);

    useEffect(() => {
        Axios.get('/tipos/list-type')
            .then(({ data }) => {
                setTipo(data);
            })
            .catch((error) => {
                console.log(error);
            });
    }, []);

    const Data2Table = () => {
        return tipo.map((res, i) => {
            return <TypeTable obj={res} key={i} />;
        });
    };

    return (

        <div>
           <div className="fila-crear">
            <hr/>
            </div>          
            <div className="table-wrapper">
                <Table striped bordered hover>
                    <thead>
                        <tr>
                            <th>Nombre</th>
                            <th>Estado</th>
                            <th>Fecha de creacin</th>
                            <th>Fecha de Modificacin</th>
                            <th>Acciones</th>
                        </tr>
                    </thead>
                    <tbody>{Data2Table()}</tbody>
                </Table>
            </div>
        </div>
    );
};

export default TypeList